import 'isomorphic-fetch';

import {
    TOBNUM_REQUEST,
    TOBNUM_RECEIVE,
} from '../constants/tobNum';

export const tobNumRequest = () => ({
    type: TOBNUM_REQUEST
});

export const tobNumReceive = stories => ({
    type: TOBNUM_RECEIVE,
    data: stories
});

export const tobNumPost = () => (dispatch) => {
    dispatch(tobNumRequest());
    return fetch('http://0.0.0.0:9527/api/tob')
    .then((response) => {
        if (response.status >= 400) {
            console.log('net work fail');
        }
        return response.json();
    })
    .then((json) => {
        dispatch(tobNumReceive(json));
    }).catch(ex =>
        console.log('parsing failed', ex)
    );
};



